import React from 'react'
import './Poland.scss';
import ContactBottom from './ContactBottom';
import Carousel from 'react-bootstrap/Carousel';

import logo from './img/premiumYachts.jpg'

import img1 from './img/POLSKA/rejsypolska(1).jpg'
import img1s from './img/POLSKA/rejsypolska(1)s.jpg'
import img2 from './img/POLSKA/rejsypolska(2).jpeg'
import img2s from './img/POLSKA/rejsypolska(2)s.jpeg'
import img3 from './img/POLSKA/rejsypolska(13).jpg'
import img3s from './img/POLSKA/rejsypolska(13)s.jpg'

import img4 from './img/POLSKA/rejsypolska(2).jpg'
import img4s from './img/POLSKA/rejsypolska(2)s.jpg'
import img5 from './img/POLSKA/rejsypolska(3).jpg'
import img5s from './img/POLSKA/rejsypolska(3)s.jpg'
import img6 from './img/POLSKA/rejsypolska(8).jpg'
import img6s from './img/POLSKA/rejsypolska(8)s.jpg'

import img7 from './img/POLSKA/rejsypolska(5).jpg'
import img7s from './img/POLSKA/rejsypolska(5)s.jpg'
import img8 from './img/POLSKA/rejsypolska(6).jpg'
import img8s from './img/POLSKA/rejsypolska(6)s.jpg'
import img9 from './img/POLSKA/rejsypolska(7).jpg'
import img9s from './img/POLSKA/rejsypolska(7)s.jpg'

const Poland = () => {

    const imgSet1 = [
        { id: 1, path: img1, pathS: img1s, },
        { id: 2, path: img2, pathS: img2s, },
        { id: 3, path: img3, pathS: img3s, },
    ]

    const carouselItems1 = imgSet1.map(item => {
        return (
            <Carousel.Item key={item.id}>
                <picture>
                    <source media="(min-width: 461px)" srcSet={item.path} />
                    <img src={item.pathS} className="d-block w-100" alt="..." />
                </picture>
            </Carousel.Item>
        )
    })

    const imgSet2 = [
        { id: 1, path: img4, pathS: img4s, },
        { id: 2, path: img5, pathS: img5s, },
        { id: 3, path: img6, pathS: img6s, },
    ]

    const carouselItems2 = imgSet2.map(item => {
        return (
            <Carousel.Item key={item.id}>
                <picture>
                    <source media="(min-width: 461px)" srcSet={item.path} />
                    <img src={item.pathS} className="d-block w-100" alt="..." />
                </picture>
            </Carousel.Item>
        )
    })

    const imgSet3 = [
        { id: 1, path: img7, pathS: img7s, },
        { id: 2, path: img8, pathS: img8s, },
        { id: 3, path: img9, pathS: img9s, },
    ]

    const carouselItems3 = imgSet3.map(item => {
        return (
            <Carousel.Item key={item.id}>
                <picture>
                    <source media="(min-width: 461px)" srcSet={item.path} />
                    <img src={item.pathS} className="d-block w-100" alt="..." />
                </picture>
            </Carousel.Item>
        )
    })

    return (
        <div className="poland">
            <h1>INDYWIDUALNE REJSY NA LUKSUSOWYCH JACHTACH MOTOROWYCH</h1>
            <div className="flex1">
                <div className="logo">
                    <img src={logo} alt="premium yachts logo" />
                </div>
                <div className="text1">
                    <p>Marka Premium Yachts powstaa w 2010 roku i od tego czasu cieszy si bardzo du popularnoci wrd klientw z caego wiata. Sezonow baz naszej dziaalnoci jest Marina w Sopocie zlokalizowany na kocu sopockiego molo. Jest to idealne miejsce na rozpoczcie morskiej przygody oraz wspaniaa baza suca jako punkt wyjciowy do innych atrakcji. Rejsy na naszych luksusowych jachtach to niezapomniane przeycie gwarantujce moc pozytywnych wrae . Do kadego zapytania podchodzimy indywidualnie, a dziki wsppracy z firmami eventowymi, hotelami, restauracjami i klubami jestemy w stanie zorganizowa pobyt w Trjmiecie szyty na miar zgodnie z Pastwa wymagania. Planujesz wizyt w Trjmiecie i chcesz uatrakcyjni swj pobyt? Nie ma lepszego pomysu ni skontaktowa si z nami i wyruszy w rejs peen zabawy i niezapomnianych emocji. Zapraszamy!</p>
                </div>
            </div>
            <div className="flex1">
                <div className="carousel orderCarousel">
                    <Carousel controls={false} indicators={false}>
                        {carouselItems1}
                    </Carousel>
                </div>
                <div className="text1">
                    <h2>Organizujemy:</h2>
                    <ul>
                        <li>rejsy rodzinne po Zatoce Gdaskiej</li>
                        <li>rejsy indywidualnie dopasowane do ycze Wynajmujcego</li>
                        <li>rejsy na romantyczne zachody soca przy molo w Sopocie</li>
                        <li>imprezy okolicznociowe (urodziny, wieczory panieskie, rocznice)</li>
                        <li>spotkania biznesowe w luksusowej, komfortowej i dyskretnej atmosferze</li>
                        <li>sesje zdjciowe, reklamowe i marketingowe</li>
                        <li>eventy firmowe i imprezy integracyjne</li>
                        <li>akcje promocyjne</li>
                    </ul>
                </div>
            </div>
            <div className="flex1">
                <div className="carousel">
                    <Carousel controls={false} indicators={false}>
                        {carouselItems2}
                    </Carousel>
                </div>
                <div className="text1">
                    <p>Gwarantujemy indywidualne podejcie do kadego zlecenia!
        Moliwo czarteru na godziny, dni, tygodnie!
Moliwo personalizacji jednostek w celach promocyjnych!</p>
                </div>
            </div>
            <div className="flex1">
                <div className="carousel orderCarousel">
                    <Carousel controls={false} indicators={false}>
                        {carouselItems3}
                    </Carousel>
                </div>
                <div className="text1">
                    <p>Gwarantujemy wspania zabaw i niezapomniany klimat wsplnych rejsw pozostajcych w pamici na cae ycie. Zapraszamy na pokad podczas rodzinnych wycieczek po Zatoce, imprez okolicznociowych, wieczorw panieskich, spotka biznesowych, rejsw firmowych oraz eventw, ktrych w tym sezonie nie bdzie brakowa w Trjmiecie.</p>
                </div>
            </div>
            <ContactBottom />
        </div >
    )
}

export default Poland
