import React from 'react'
import './Fendertex.scss';
import ContactBottom from './ContactBottom';
import Carousel from 'react-bootstrap/Carousel';

import logo from './img/fendertex/fendertexLogo2.jpg'

import img1 from './img/fendertex/73387330_143843420180435_3154112044718803727_n.jpg'
import img2 from './img/fendertex/75388160_108402873967238_6908105057644838912_o.jpg'
import img3 from './img/fendertex/78061741_108448963962629_1501454681883279360_n.jpg'
import img4 from './img/fendertex/78700405_118735696265759_3113567439506374656_o.jpg'
import img5 from './img/fendertex/79978744_122120562593939_5359801783029858304_o.jpg'
import img6 from './img/fendertex/xca309866dcc6f99e9d0aefd47137ff73ff996427_slider-textile-fenders-fendertex.jpg.pagespeed.ic.PM_JYoJ14M.jpg'
import img7 from './img/fendertex/xf1c21f5d7793b00c2a353fc4f73a917d7bba02fe_pare-battages-cylindrique-gris-fendertex-slider.jpg.pagespeed.ic.mqjCHqfQeL.jpg'
import img8 from './img/fendertex/fendertex-fenders-002.jpg'
import img9 from './img/fendertex/81292482_128832878589374_7541409328448667648_o.jpg'

import img10 from './img/fendertex/custom-colors-001.png'
import img11 from './img/fendertex/fendertex-cylindrical-fender-001.jpg'
import img12 from './img/fendertex/fendertex-spherical-fender-001.jpg'
import img13 from './img/fendertex/fendertex-tubular-fender-001.jpg'

const Fendertex = () => {

    return (
        <div className="fendertex">
            <div className="logo">
                <img src={logo} alt="fendertex logo" />
            </div>
            <div className="flex1">
                <div className="text1">
                    <p>Fendertex to jedyne na rynku ultralekkie odbijacze, o podwyszone wytrzymaoci, ktre mog by indywidualnie dopasowane do twojego jachtu. Dziki zaawansowanym badaniom firmie Fendertex udao si opatentowa specjalny materia dziki ktremu odbijacze Fendertex s 10 razy bardziej wytrzymae na nacisk w porwnaniu do standardowych odbijaczy, a ich waga jest 95% nisza od standardowych odbijaczy z PCV. Nowoczesna konstrukcja pozwala na bardzo atwe pompowanie i spuszczenia powietrza z odbijaczy dziki czemu mona oszczdzi duo miejsca dedykowanego na skadowanie standardowych odbijaczy. Dziki moliwoci penej personalizacji odbijaczy i ich ochraniaczy, moesz dopasowa kolor jak i grafik idealnie pod swj jacht.</p>
                </div>
                <div className="carousel">
                    <Carousel controls={false} indicators={false}>
                        <Carousel.Item>
                            <img src={img1} class="d-block w-100" alt="..." />
                        </Carousel.Item>
                        <Carousel.Item>
                            <img src={img2} class="d-block w-100" alt="..." />
                        </Carousel.Item>
                        <Carousel.Item>
                            <img src={img3} class="d-block w-100" alt="..." />
                        </Carousel.Item>
                        <Carousel.Item>
                            <img src={img4} class="d-block w-100" alt="..." />
                        </Carousel.Item>
                        <Carousel.Item>
                            <img src={img5} class="d-block w-100" alt="..." />
                        </Carousel.Item>
                        <Carousel.Item>
                            <img src={img6} class="d-block w-100" alt="..." />
                        </Carousel.Item>
                        <Carousel.Item>
                            <img src={img7} class="d-block w-100" alt="..." />
                        </Carousel.Item>
                        <Carousel.Item>
                            <img src={img8} class="d-block w-100" alt="..." />
                        </Carousel.Item>
                        <Carousel.Item>
                            <img src={img9} class="d-block w-100" alt="..." />
                        </Carousel.Item>
                    </Carousel>
                </div>
            </div>
            <div className="flex1">
                <div className="text1 orderText">
                    <h2>Zalety odbijaczy Fendertex:</h2>
                    <ul>
                        <li>najlejsze odbijacze na wiecie</li>
                        <li>zewntrzna warstwa zrobiona z jednego kawaka opatentowanego materiau</li>
                        <li>atwo pompowania i spuszczania powietrza co oszczdza miejsca podczas skadowania</li>
                        <li>bardzo wysoka wytrzymao na naprenia</li>
                        <li>zachowanie waciwoci i ksztatu w szerokiej skali temperatury</li>
                        <li>dostpne w trzech ksztatach i dziewiciu standardowych kolorach</li>
                        <li>specjalne kolory, rozmiary i grafiki dostpne na zamwienie</li>
                        <li>szeroka paleta akcesoriw jak ochraniacze, osony metalowych uchwytw, liny, pompki i grafiki</li>
                        <li>wydajne pompki do atwego pompowania i wypuszczania powietrza</li>
                        <li>moliwo wyszycia logo lub nazwy jachty na ochraniaczach</li>
                    </ul>
                </div>
                <div className="carousel contain">
                    <Carousel controls={false} indicators={false}>
                        <Carousel.Item>
                            <img src={img10} class="d-block w-100" alt="..." />
                        </Carousel.Item>
                        <Carousel.Item>
                            <img src={img11} class="d-block w-100" alt="..." />
                        </Carousel.Item>
                        <Carousel.Item>
                            <img src={img12} class="d-block w-100" alt="..." />
                        </Carousel.Item>
                        <Carousel.Item>
                            <img src={img13} class="d-block w-100" alt="..." />
                        </Carousel.Item>
                    </Carousel>
                </div>
            </div>
            <div className="links">
                <h2>Obejrzyj prezentacj odbijaczy:</h2>
                <div className="link1">
                    <p><a target="_blank" rel="noopener noreferrer" href="https://www.youtube.com/watch?v=8LFl0wi6q6w">Film 1</a></p>
                </div>
            </div>
            <ContactBottom />
        </div>
    )
}

export default Fendertex
